import React from 'react';

import TopSection from '../components/TopSection';
import Section from '../components/Section';

export default ({ classes, data }) => {
    return (
        <div>
            <TopSection
                sizesBackground={data.imageBackground.childImageSharp.sizes}>
                <h1>{data.contents.childStringsToml.usecase.title}</h1>
                <p>{data.contents.childStringsToml.usecase.description}</p>
            </TopSection>
            <Section>
                usecases
            </Section>
        </div>
    );
};

export const query = graphql`
    query IndexUsecaseQuery {
        ...IndexLayoutFragment
        ...IndexUsecaseFragment
    }

    fragment IndexUsecaseFragment on RootQueryType {
        imageBackground: file(relativePath: { eq: "bg_usecase.jpg" }) {
            childImageSharp {
                sizes(maxWidth: 984) {
                    ...GatsbyImageSharpSizes
                }
            }
        }

        contents: file(relativePath: { eq: "strings.toml" }) {
            childStringsToml {
                usecase {
                    title
                    description
                }
            }
        }
    }
`;



